<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process188"></a>

<p><br></p>
<p><span class="rvts17">章节探索</span><span class="rvts50"> </span><span class="rvts17">石墓花园</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:551Y:177</span><span class="rvts21">，石墓花园</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">探索石墓花园</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1210.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">石墓花园在死神海岸的南部，与浮木城隔着一条河，可以从骑士堡垒通过，也可以在第四章支线任务</span><span class="rvts20"></span><span class="rvts21">此路不同</span><span class="rvts20"></span><span class="rvts21">中，通过放下对面的吊桥通过。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1211.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">石墓花园由一个叫做了瑞克（</span><span class="rvts20">Ryker</span><span class="rvts21">）的死灵法师把守，而且他还是个可以帮助我们增强源力的角色。在瑞克身上，隐藏了整个石墓花园的秘密。那些被他控制的守卫表面上活着，其实已经灵魂出窍，被束缚于不死的傀儡之身。</span>
<br><span class="rvts21">探索石墓花园，可以完成很多额外的支线，获取经验并提升等级。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



